import {reqLotteryList, reqSavePrize, reqBrushNum, reqHaveNum} from '@/network/lottery.js'


//计数器
var interval = null;
//值越大旋转时间越长  即旋转速度
// var intime = 50;
export default {
	data() {
		return {
			color: [0.5, 0.5, 0.5, 0.5, 0.5, 0.5, 0.5, 0.5],
			images: [],
			clickLuck: 'clickLuck',
			luckPosition: 0,
			initChangeNum: 0,
			luckyProList:{},
			tipPrefix:'恭喜您获得“',
			tipPostfix:'”！',
			modalTip: '',
			
			testImg: ''
			
		}
	},
	components:{
		
	},
	created() {
		// uni.hideLoading()
		this.loadAnimation();
		 reqLotteryList(res=>{
			this.luckyProList = res.data
		})
		reqHaveNum(res=>{
			this.initChangeNum = res.data.brush_num
			this.$emit('changeNum', this.initChangeNum)
		}, {open_id:uni.getStorageSync('openid')})
	},
	props:{
		intime:{
			default: 50,
			type: Number
		},
		
		
	},
	methods: {
		input: function(e) {
			var data = Number(e.detail.value);
			this.luckPosition = data;
		},
		//按luckyProList分配的概率生成对应的一个随机luckPosition值（中奖位置索引）
		computeLucyList(){
			let tmpList = []
			let total = 0
			
			for(let i in this.luckyProList){
				total += this.luckyProList[i].prob
				tmpList.unshift(total);
			}
			tmpList.push(-1)
			let ranNum = Math.random() // 0-1的随机数
			console.log(tmpList);
			console.log(ranNum)
			
			tmpList.every((num,i)=>{
				if(ranNum>num){
					this.luckPosition = 8-i
					
					return false
				}else{
					return true
				}
				
				
			})
			
			console.log(this.luckPosition)  //中奖结果已有
		},
		toReqSaveRecord(){
			let param = {
				name:this.luckyProList[this.luckPosition]['tip'],
				open_id: uni.getStorageSync('openid'),
				phone: uni.getStorageSync('phone')
			}
			reqSavePrize((res)=>{console.log(res)}, param)
			reqBrushNum((res)=>{console.log(res)}, {open_id: param.open_id})
		},
		//点击抽奖按钮
		clickLucks() {

			if (this.clickLuck == 'clickLuck') {
				console.log('cishu', this.initChangeNum)
				// 判断抽奖次数是否为0
				if (this.initChangeNum<=0) {
					uni.showModal({
						title: '提示',
						content: '您已抽过奖品了',
						showCancel: false,
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
					return;
				}else{
					this.computeLucyList()
					this.toReqSaveRecord()
					
					this.initChangeNum --
					this.$emit('changeNum', this.initChangeNum)
				}

				//设置按钮不可点击
				// e.btnconfirm = '../../static/sherpa-jiugongge/dianjichoujiangd.png';
				this.clickLuck = '';

				//清空计时器
				clearInterval(interval);
				var index = 0;
				//循环设置每一项的透明度
				interval = setInterval(()=> {
					if (index > 7) {
						index = 0;
						// e.color[7] = 0.5
						this.$set(this.color, 7, 0.5);
					} else if (index != 0) {
						// e.color[index - 1] = 0.5
						this.$set(this.color, index-1, 0.5);
					}
					this.$set(this.color, index, 1);
					index++;
				}, this.intime);
				//模拟网络请求时间  设为两秒
				var stoptime = 2000;
				setTimeout(()=> {
					this.stop(this.luckPosition);
				}, stoptime)
			}
		},
		stop(which) {
			var e = this;
			//清空计数器
			clearInterval(interval);
			//初始化当前位置
			var current = -1;
			var color = e.color;
			for (var i = 0; i < color.length; i++) {
				if (color[i] == 1) {
					current = i;
				}
			}
			//下标从1开始
			var index = current + 1;
			e.stopLuck(which, index, this.intime, 10);
		},
		stopLuck(which, index, time, splittime) {
			var e = this;
			//值越大出现中奖结果后减速时间越长
			setTimeout(()=> {
				//重置前一个位置
				if (index > 7) {
					index = 0;
					// e.color[7] = 0.5
					e.$set(e.color, 7, 0.5)
				} else if (index != 0) {
					// e.color[index - 1] = 0.5
					e.$set(e.color, index - 1, 0.5)
				}
				//当前位置为选中状态
				e.color[index] = 1;
				//如果旋转时间过短或者当前位置不等于中奖位置则递归执行
				//直到旋转至中奖位置
				if (time < 400 || index != which) {
					//越来越慢
					splittime++;
					time += splittime;
					//当前位置+1
					index++;
					e.stopLuck(which, index, time, splittime);
				} else {
					//1秒后显示弹窗
					setTimeout(()=> {
						// if (which == 0) {
							//中奖
							// uni.showModal({
							// 	content: this.tipPrefix + e.luckyProList[which+1].tip,
							// 	showCancel: false,
							// 	confirmColor: "#F8C219",
							// 	success: res => {
							// 		if (res.confirm) {
							// 			//设置按钮可以点击
							// 			// e.btnconfirm = '../../static/sherpa-jiugongge/dianjichoujiang.png';
							// 			e.clickLuck = 'clickLuck';
							// 			e.loadAnimation();
							// 		}
							// 	},
							// 	fail: () => {},
							// 	complete: () => {}
							// });
							// this.$refs.modal.modalShow = true
							this.clickLuck = 'clickLuck';
							this.loadAnimation();
							
							let onload = {
								luckPosition: this.luckPosition,
								tip:  this.tipPrefix + this.luckyProList[which].tip + this.tipPostfix,
								isWin: true,
								// p1 : '扫码关注公众号',
								// p2: '客服联系您领奖'
							}
							if(which===1){
								onload.isWin = false
								onload.tip = '很遗憾您未中奖，谢谢参与！'
								
								// onload.p1 = '更多惊喜'
								// onload.p2 = '关注公众号'
							}
							
							this.$EventBus.$emit('closeModal', onload)
						// } 
						// else {
						// 	//未中奖
						// 	uni.showModal({
						// 		content: '很遗憾未中奖',
						// 		showCancel: false,
						// 		confirmColor: "#F8C219",
						// 		success: res => {
						// 			if (res.confirm) {
						// 				//设置按钮可以点击
						// 				e.btnconfirm = '../../static/sherpa-jiugongge/dianjichoujiang.png';
						// 				e.clickLuck = 'clickLuck';
										
						// 				e.loadAnimation();
						// 			}
						// 		},
						// 		fail: () => {},
						// 		complete: () => {}
						// 	});
						// }
					}, 1000);
				}
			}, time);
		},
		loadAnimation() {
			var e = this;
			var index = 0;
			if (interval == null) {
				interval = setInterval(()=> {
					if (index > 7) {
						index = 0;
						// e.color[7] = 0.5
						e.$set(e.color, 7, 0.5)
					} else if (index != 0) {
						// e.color[index - 1] = 0.5
						e.$set(e.color, index - 1, 0.5)
					}
					e.color[index] = 1;
					e.$set(e.color, index, 1)
					index++;
				}, 1000);
			}
		}
	},
	
}
